import React from 'react';
import PropTypes from 'prop-types';

import TitleBar from './TitleBar';
import DaysSupply from './DaysSupply';
import FillDate from './FillDate';
import PastFills from './PastFills';
import DaysEarly from './DaysEarly';

//import calculate from '../logic/calculate';
//import './App.css';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import { withStyles } from 'material-ui/styles';
import Card, { CardContent } from 'material-ui-next/Card'; // v1.x
import Typography from 'material-ui-next/Typography'; // v1.x

class App extends React.Component {
  constructor(props) {
    super(props);
	
    const today = new Date();
    today.setHours(0, 0, 0, 0);
	const past = new Date();
	past.setMonth(today.getMonth() -1);
	
    this.state = {
      days: null,
	};
  }

  handleClick = (days) => {
    //this.setState(calculate(this.state, buttonName));
	console.log(days);
  }

  render() {
    return (
      <div className="component-app">
	  <MuiThemeProvider>
	    <TitleBar />

		<Card style={{width: '95%', margin: '10px auto'}}>
		  <CardContent> 
			  <FillDate /> &nbsp; <DaysSupply clickHandler={this.handleClick} /> 
		  </CardContent>
		</Card>

		<Card style={{width: '95%', margin: '10px auto'}}>
		  <CardContent> 
			<Typography>Past Fills</Typography>
			<PastFills /> 
		  </CardContent>
		</Card>

		<Card style={{width: '95%', margin: '10px auto'}}>
		  <CardContent> 
			  <DaysEarly value={this.state.days || '#'} />
		  </CardContent>
		</Card>

	  </MuiThemeProvider>
      </div>
    );
  }
}
export default App;



