import React from 'react';
import DatePicker from 'material-ui/DatePicker';

const optionsStyle = {
  maxWidth: 255,
  marginRight: 'auto',
};

export default class PastFills extends React.Component {
  constructor(props) {
    super(props);

  }
  
  handlePastFills = (event, value) => {
	  // create an additional date 
	  // update 
	  //console.log(this.state.days);
	  //console.log(this.value);
  }
  
  render() { 
	return (
		<div style={optionsStyle}> 
			<DatePicker
				floatingLabelText="Fill Date"
				autoOk={true}
				firstDayOfWeek={0}
				onChange={this.handlePastFills}
			/>
		</div>
	);
  }
}



