import React from 'react';
import DatePicker from 'material-ui/DatePicker';

const optionsStyle = {
  maxWidth: 120,
  marginRight: 'auto',
  display: 'inline-block',
};

export default class FillDate extends React.Component {
  constructor(props) {
    super(props);
  }
  
  render() { 
	return (
		<div style={optionsStyle}> 
			<DatePicker
				floatingLabelText="Fill Date"
				autoOk={true}
				firstDayOfWeek={0}
			/>
		</div>
	);
  }
}



