import React from 'react';
import PropTypes from 'prop-types';

import { withStyles } from 'material-ui-next/styles';
import Input, { InputLabel } from 'material-ui-next/Input';
import { MenuItem } from 'material-ui-next/Menu';
import { FormControl, FormHelperText } from 'material-ui-next/Form';
import Select from 'material-ui-next/Select';

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2,
  },
});

class DaysSupply extends React.Component {
  state = {
    days: '',
  };

  handleChange = event => {
    this.setState({ days: event.target.value });
  };

  render() {
    const { classes } = this.props;

    return (
      <form style={{display: 'inline-block !important'}} className={classes.container} noValidate autoComplete="off">
        <FormControl className={classes.formControl}>
          <InputLabel htmlFor="days-input">Days Supply</InputLabel>
          <Input id="days-input" value={this.state.days} onChange={this.handleChange} />
          <FormHelperText>any number of days</FormHelperText>
        </FormControl>
        <FormControl className={classes.formControl}>
          <InputLabel htmlFor="days-select">Days Supply</InputLabel>
          <Select
            value={this.state.days}
            onChange={this.handleChange}
            input={<Input name="days" id="days-select" />}
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            <MenuItem value="7">7</MenuItem>
            <MenuItem value="14">14</MenuItem>
            <MenuItem value="28">28</MenuItem>
            <MenuItem value="30">30</MenuItem>
            <MenuItem value="60">60</MenuItem>
            <MenuItem value="90">90</MenuItem>
            <MenuItem value="120">120</MenuItem>
          </Select>
        </FormControl>
	  </form>
	);
  }	
}
DaysSupply.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(DaysSupply);



