import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui-next/styles';
import Typography from 'material-ui-next/Typography';

class DaysEarly extends React.Component {
  render() {
    return (
	  <div>
        <Typography variant="headline" component="h3">
		{this.props.value} Days Early Available
        </Typography>
        <Typography component="p">
          The earliest this script can be filled is {this.props.value} days early: DD/MM/YYYY
        </Typography>
      </div>
	);
  }
}

DaysEarly.propTypes = {
  value: PropTypes.string,
};

export default DaysEarly;


